    width: 1200px;
    height: 0px;
    
    border: 1px solid #ACACAC;
    
    flex: none;
    order: 1;
    flex-grow: 0;     
}


@media (max-width: 1452px) {
    body {
        overflow-x: hidden;
    }

    .landing_page {
        gap: 3rem;
    }

    .landing_page_header {
        flex-direction: column;
        gap: 3rem;
        margin-bottom: 4rem;
    }

    .landing_page_welcome_primary {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-content: center;
        flex-wrap: wrap;
    }
    .welcome_left {
        align-items: center;
    }

    .split1_header {
        text-align: center;
        justify-content: center;
        margin-bottom: 4rem;
    }

    .top {
        margin-bottom: 2rem;
    }

    .middle {
        margin-bottom: 2rem;
    }
    
    .bottom {
        margin-bottom: 4rem;
    }

    .landing_page_emojis {
        flex-direction: column;
        margin: 4rem 1rem -1rem 1rem;
    }

    .emojis_left {
        padding-bottom: 4rem;
    }
    .emojis_middle {
        padding-bottom: 4rem;
    }

    .landing_page_aboutus {
        flex-direction: column;
    }

    .footer_content {
        flex-direction: column;
    }

    .landing_page_footer {
        display: none;
    }
    .main_image_2 {
        display: none;
    }
    .aboutus_explanation {
        align-items: center;
    }

    .explanation_top {
        margin: 0rem 2rem 0rem 2rem;
    }
    .explanation_middle {
        margin: 0rem 2rem 0rem 2rem;
    }
    .explanation_bottom {
        margin: 0rem 2rem 0rem 2rem;
    }
}

html, body {
    width: 100%;
    min-height: 100%;
}

